body {
  background: #f5f6fa;
  font-family: 'Segoe UI', Arial, sans-serif;
  margin: 0;
  padding: 0;
}
h2 {
  color: #222;
}
table {
  border-radius: 8px;
  box-shadow: 0 2px 8px #eee;
}
input, button {
  font-size: 1rem;
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #ddd;
}
button {
  background: #007bff;
  color: white;
  border: none;
  cursor: pointer;
  transition: background 0.2s;
}
button:hover {
  background: #0056b3;
}
th, td {
  padding: 10px;
  text-align: left;
}